:root {
  --primaryColor: hsl(199, 100%, 33%);
  --SecondaryColor: hsl(187, 85%, 43%);
  --GradientColor: linear-gradient(
    to right,
    hsl(187, 50%, 56%),
    hwb(199 57% 11%)
  );
  --whiteColor: hsl(0, 0, 100%);
  --blackColor: hsl(201, 33%, 16%);
  --textColor: hsl(240, 4%, 36%);
  --WhiteColorDeam: hsl(0, 0, 93%);
  --grayText: rgb(190, 190, 190);
  --inputcolor: rgb(239, 239, 239);
  --BodyText: rgb(240, 240, 240);
  --carBG: rgb(225, 225, 225);
}
.home {
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;

  .overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    z-index: 1;
    mix-blend-mode: hard-light;
  }
  video {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    object-fit: cover;
  }
  .homeContent {
    width: 100%;
    height: max-content;
    padding: 8rem 1.5rem;
    display: flex;
    row-gap: 3rem;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    margin: auto;
    color: var(--whiteColor);
    z-index: 100;
  }
  .textDiv {
    padding: 2rem 0;
    .smallText {
      font-size: 13px;
      text-transform: uppercase;
    }
    .homeTitle {
      position: relative;
      font-size: 1.8rem;
    }
  }
  .cardDiv{
    
    position: relative;
    padding: 2rem 1rem;
    background-color:white;
    border-radius: 10px;
    gap: 1rem;
    width: 100%;
    label {
      display: block;
      color: var(--textColor);
      padding-bottom: 10px;
      font-size: 13px;
      font-weight: 500;
    }
    .label_total {
      justify-content: center;
      align-items: center;
      .total {
        color: var(--textColor);
        font-size: 18px;
        padding-bottom: 10px;
      }
    }
    .input {
      // background-color: red;
      height: 40px;
      // width: 100%;
      padding: 0 1rem;
      background-color: var(--inputcolor);
      border-radius: 3rem;
    input{
        background: none;
        border: none;
        outline: none;
        width: 100%;
        font-size: 13px;
        font-weight: 500;
        &::placeholder{
            font-size: 13px;
            color: black;
        }
    }
    icon{
        font-size: 18px;
        color: var(--primaryColor)
        !important;
        margin-left: 10px;
        
    }
    .input[type=range]{
        background: blue;
    }
    }

    .searchOptions{
        // position: absolute;
        background: var(--GradientColor);
        width:100%;
        bottom: -0.7rem;
        column-gap: .8rem;
        padding:10px 2rem;
        border-radius: 3rem;
        left: 50%;
        // transform: translate(50%);
        // display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        &:hover{
            background:var(--primaryColor);
        }
        .icon{
            font-size: 18px;
        }
        span{
            font-size: 13px;
        }
    }

  }
  .homeFooterIcons{
    // width:max-content;
    padding: 10px;
    justify-content: center;
    // background: rgb(255, 255, 255,.3);
    border-radius: 20px;
    
   
  
  .icon{
    font-size: 22px;
    margin: 0 5px;
    color: var(--whiteColor);
   

  }
}
}
@media screen and (min-width:500px) {
 .home{
  .homeContent{
    .textDiv{
      .smallText{
        font-size: 16px;
      }
      .homeTitle{
        position: relative;
        font-size: 2.3rem;
        width: max-content;
        &::after{
          position: absolute;
          content: '';
          background-color: var(--SecondaryColor);
          // background-color: aqua;
          height:2px ;
          right: 0;
          width: 150px;
          bottom:-5px;
          z-index: -1;
        }
      }
    }
    .cardDiv{
      grid-template-columns: repeat(2,1fr);
    }

  }
 }


}
@media screen and (min-width:660px) {
 .home{
  .homeContent{
   
    .cardDiv{
      grid-template-columns: repeat(3,1fr);
      padding: 1rem 3rem;
    }

  }
 }


}
@media screen and (min-width:940px) {
 .home{
  // height: 70vh;
  .homeContent{
   
    .cardDiv{
      grid-template-columns: repeat(4,1fr);
      padding: 1rem 3rem;
    }

  }
 }


}
@media screen and (min-width:1240px) {
 .home{
  // height: 80vh;
  .homeContent{
   padding-left: 0;
   padding-right: 0;
    .cardDiv{
      grid-template-columns: repeat(4,1fr);
      padding: 2rem 1rem;
      .input{
        margin-bottom: 1rem;
      }
      .searchOptions{
        bottom: -1rem;
        padding: 8px 1rem;
        span{
          font-size: 17px;
        }
      }
    }

  }
 }


}


